









export default {
	data() {
		return {
			userDTO: {
				mobile: '',
				password: '',
			},
			remmberPsw: true
		};
	},
	onLoad() {
		// 		uni.setNavigationBarTitle({
		// 			title: '登录'
		// 		});
	},
	methods: {
		register: function(userDTO) {
			var _this = this;
			// console.log(userDTO.mobile + ',' + userDTO.password);
			if (_this.userDTO.password != _this.userDTO.passwordAgain) {
				uni.showToast({
					title: '两次密码输入不一致',
					duration: 1000,
					mask: true
				})
			} else {
				uni.request({
					url: 'http://47.102.201.109:8080/api/user/insertDto',
					// url: 'http://172.20.10.4:8080/api/user/sign_in',
					method: 'POST',
					data: {
						mobile: userDTO.mobile,
						password: userDTO.password,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						// console.log(res.data.data.mobile);
						if (res.data.code == 0) {
							uni.showModal({
								title: '注册成功',
								success: function(res) {
									if (res.confirm) {
										uni.navigateBack({
											delta: 1
										})
									} else if (res.cancel) {
										uni.navigateBack({
											delta: 1
										})
									}
								}
							});
						}
						//登录失败，弹出各种原因
						else {
							uni.showModal({
								title: '注册失败',
								content: res.data.msg
							});
						}
					}
				});
			}

		},
	}
};
